<template>
  <div class="cartoon-form">
    <div class="cartoon-form__content">
      <div class="mt__30 cartoon-form__content__title">
        - 开课3天内 对课程不满意 无条件退款 -
      </div>
      <!-- 手机号 -->
      <div class="cartoon-form__content__item mt__40">
        <p class="ml__10 mr__20">+86</p>
        <input
          ref="focusInput"
          placeholder="请输入手机号"
          class="form-line"
          v-model.trim="phone" />
      </div>
      <!-- 验证码 -->
      <div class="cartoon-form__content__item mt__10">
        <input
          placeholder="请输入验证码"
          v-model.trim="code" />
        <CountDownBtn
          class="pl__20 phone-code form-line"
          :phone="phone"></CountDownBtn>
      </div>
      <!-- 支付按钮 -->
      <div class="cartoon-form__content__submit-btn mt__30" @click="goPay">限量抢购</div>
      <div class="cartoon-form__content__submit-text mt__30">
        *注：抢购后，班主任将全程指导学习 今日仅限100抢购名额
      </div>
    </div>
    <div class="cartoon-form__jumpPage">
      <div>
        <div class="cartoon-form__text-box">
          <span class="cartoon-form__text-box__money">￥12</span>
          <!-- <span class="cartoon-form__text-box__original">市场价￥199</span> -->
          <div>今日仅限100个抢购名额</div>
        </div>
        <div class="cartoon-form__submit-btn" @click="goPay">限量抢购</div>
      </div>
    </div>
    <!-- 挽留弹窗 -->
    <BackDialog
      @backDiaLogSubmit="backDiaLogSubmit"></BackDialog>
    <!-- 支付类型弹窗 -->
    <PayTypeDialog
      @clickCloseIcon="closePayTypeDialog"
      @payTypeClick="payLogAndSubmitPay"
      :pay-dialog-config="payDialogConfig"></PayTypeDialog>
  </div>
</template>

<script>
import PayTypeDialog from '@/components/BaiduABBusniess/payTypeDialog' // 支付方式选择弹窗
import CountDownBtn from '@/components/CountDownBtn' // 倒计时
import BackDialog from '@/components/BaiduABBusniess/backDialog' // 挽留弹窗
import vue from '@/utils/busVue'
import { openPayTypeDialog, payTypeClick, closePayTypeDialog } from '@/helper/payTypeDialog'
import { Dialog } from 'vant'
import { initPhoneForm } from '@/helper/backDiaLog'
import { submitPay } from '@/helper/payMent'
import { getPayQuery } from '@/helper/bzanPayMentBack'
import { isPhone } from '@/utils'

export default {
  name: 'CartoonForm',
  components: { CountDownBtn, PayTypeDialog, BackDialog },
  data () {
    return {
      phone: '', // 手机号
      code: '', // 验证码
      paytype: 2, // 支付类型
      successPag: 'Officals', // 支付后要跳转的成功页面
      payDialogConfig: {
        dialogMark: false
      } // 支付弹窗
    }
  },
  created () {
    this.initPhoneForm()
    vue.$on('focusWholePageInput', () => {
      const { phone } = this
      const msg = phone ? '手机号码有误，请重填' : '请填写手机号'
      if (!isPhone(phone)) {
        Dialog({ message: msg }).then(() => {
          this.$refs.focusInput.focus()
        })
        return
      }
      this.openPayTypeDialog()
    })
  },
  beforeDestroy () {
    vue.$off('focusWholePageInput')
  },
  methods: {
    openPayTypeDialog,
    payTypeClick,
    closePayTypeDialog,
    initPhoneForm,
    submitPay,
    getPayQuery,
    /**
     * @Date: 2021-01-14 15:03:00
     * @author: zjs
     * @description: 回调弹窗继续支付
     */
    backDiaLogSubmit () {
      const otherQuery = JSON.parse(localStorage.getItem('lastPayQuery'))
      this.submitPay({
        apiQuery: this.getPayQuery({
          ...otherQuery,
          return_url: encodeURIComponent(location.href)
        })
      })
    },
    /**
     * @Date: 2021-01-14 14:40:47
     * @author: zjs
     * @description: 支付类型埋点和支付
     */
    payLogAndSubmitPay (paytype) {
      this.payTypeClick(paytype)
      this.submitPay({
        apiQuery: this.getPayQuery({
          pay_type: paytype,
          return_url: encodeURIComponent(location.href)
        })
      })
    },
    goPay () {
      const { phone, code } = this
      const msg = phone ? '手机号码有误，请重填' : '请填写手机号'
      if (!isPhone(phone)) {
        Dialog({ message: msg }).then(() => {
          this.$refs.focusInput.focus()
        })
        return
      }
      this.$apis.payment
        .payBeforeCheck({ phone, code })
        .then(async (res) => {
          this.openPayTypeDialog()
        })
    }
  }
}
</script>
<style lang="scss">
.cartoon-form {
  width: 100%;
  padding-bottom: 30px;
  background: #f8e1bd;

  @include horVerCenter;

  .cartoon-form__content {
    width: calc(100% - 58px);
    min-height: 410px;
    padding-bottom: 30px;
    background: #fbe2da;
    border: 16px solid #fff;

    @include verCenter;

    flex-direction: column;

    .cartoon-form__content__title {
      font-size: 32px;
      font-weight: 500;
      color: #333;
      text-align: center;
    }

    .cartoon-form__content__item {
      width: calc(100% - 72px);
      height: 80px;
      border: 3px solid #704f40;
      background-color: #fff;
      font-size: 32px;
      font-weight: 400;
      color: #929292;
      display: flex;

      * {
        height: 100%;
      }

      p {
        @include horVerCenter;
      }

      .phone-code {
        font-size: 29px;
        font-weight: 400;
        color: #b92525;
      }

      input {
        background-color: transparent;
        border: 0;
        padding-left: 30px;
        width: calc(100% - 230px);
        border-radius: 0;

        &::placeholder {
          color: #929292;
          font-size: 32px;
          font-weight: 400;
        }
      }

      .form-line {
        border-radius: 0;
        border-left: 3px solid #704f40;
      }
    }

    .cartoon-form__content__submit-btn {
      @include horVerCenter;

      width: calc(100% - 72px);
      height: 90px;
      background: #fd2706;
      font-size: 48px;
      font-weight: bold;
      color: #fff;
    }

    .cartoon-form__content__submit-text {
      color: #595959;
      line-height: 37px;
      text-align: center;
      max-width: 500px;
      width: 446px;
      font-size: 27px;
      font-weight: 400;
    }
  }
}

.cartoon-form__jumpPage {
  position: fixed;
  bottom: 0;
  left: 0;
  height: 140px;
  width: 100%;
  background-color: #fff;

  & > div {
    display: flex;
    justify-content: space-between;

    .cartoon-form__text-box {
      margin-top: 15px;
      margin-left: 10px;

      .cartoon-form__text-box__money {
        font-size: 67px;
        font-weight: 800;
        font-style: italic;
        color: #f3533e;
      }

      .cartoon-form__text-box__original {
        font-size: 21px;
        font-weight: 400;
        margin-left: 10px;
        line-height: 50px;
        text-decoration: line-through;
        color: #929292;
      }

      & > div {
        padding-left: 18px;
        font-size: 27px;
        font-weight: 400;
        color: #484545;
        text-align: center;
      }
    }
  }

  .cartoon-form__submit-btn {
    @include horVerCenter;

    width: 326px;
    height: 96px;
    margin-top: 22px;
    background: #fd2706;
    border-radius: 48px;
    font-size: 48px;
    font-weight: bold;
    color: #fff;
    margin-right: 30px;
  }
}
</style>
