<template>
  <div class="bili">
    <img
      class="bili__img-item"
      v-for="(item, index) in 16"
      :key="index"
      :src="require(`@/assets/bZhan/${index+1}.jpg`)">
    <CartoonForm></CartoonForm>
  </div>
</template>

<script>
import CartoonForm from './components/CartoonForm'
import { isBuyOrder } from '@/helper/payMent'

export default {
  components: { CartoonForm },
  data () {
    return {
      timer: null // 定时器
    }
  },
  beforeDestroy () {
    this.timer && clearInterval(this.timer)
  },
  created () {
    this.isBuyOrder()
    this.timer = setInterval(async () => {
      const resultCode = await this.isBuyOrder()
      if (resultCode && resultCode !== 1000) {
        clearInterval(this.timer)
        this.timer = null
      }
    }, 3000)
    window.onpageshow = function (event) {
      if (event.persisted) {
        window.location.reload()
      }
    }
  },
  methods: { isBuyOrder }
}
</script>

<style lang="scss">
.bili{
  width: 100%;
  background-color: #F8E1BD;
  padding-bottom: 180px;
  img{
    vertical-align: middle
  }
  .bili__img-item{
   max-width: 100%;
   vertical-align: middle;
  }
}
</style>
